"use client";

import ConnectPlugin from "../connectPlugins";

// Api
import { GetAddonList } from "@/utilities/localStorage";

export default function Blogs() {
  const addonList = GetAddonList();

  return (
    addonList &&
    addonList?.["blog"] &&
    addonList?.["blog"]?.status == 1 &&
    ConnectPlugin.Blogslist && <ConnectPlugin.Blogslist />
  );
}
